<template>
    <div class="search-box">
        <iframe class="canvas-bg" sandbox="allow-scripts allow-same-origin" src="background.html" frameborder="0" allowtransparency="yes"></iframe>
        <div class="search-bar">
            <div id="search-menu">
                <label v-for="searchCategory in searchList" :key="searchCategory.index" >
                    <span @click="getSearchCategory($event)">{{searchCategory.name}}</span>
                </label>
            </div>
            <el-input
                v-model="input"
                :placeholder="placeholder"
                prefix-icon="el-icon-search"
                @keyup.enter.native="search"
                clearable>
            </el-input>
            <div id="search-menu-item">
                <label v-for="item in subList" :key="item.index">
                    <span @click="setSearchUrl($event)">{{item.text}}</span>
                </label>
            </div>
        </div>
    </div>
</template>

<script>
import searchList from '@/config/search.config.js';

export default {
    data(){
        return {
            input: '',
            placeholder: '搜索',
            searchList,
            searchCategory: '',
            searchUrl: 'https://cn.bing.com/search?q=',
        }
    },
    methods: {
        getSearchCategory(event){
            this.searchCategory = event.currentTarget.innerHTML;
        },
        setSearchUrl(event){
            const searchEngine = event.currentTarget.innerHTML;
            this.searchList.forEach(item => {
                item.subList.forEach(item => {
                    if(item.text === searchEngine){
                        this.searchUrl = item.prefixUrl;
                        this.placeholder = item.searchPrompt
                    }
                })
            })
            
        },
        search(){
            window.open(this.searchUrl+this.input);
            this.input = "";
        }
    },
    computed: {
        subList(){
            if(!this.searchCategory){
                return this.searchList[0].subList
            } else {
                return this.searchList.filter(item => {
                    return item.name === this.searchCategory
                })[0].subList
            }
        }
    }
}
</script>

<style>
.search-box{
    position: relative;
    margin: 0;
    min-height: 200px;
}

.search-box iframe{
    position: absolute;
    min-height: 100%;
    min-width: 100%;
}

.search-box .search-bar{
    padding: 10% 20%;
}

.search-box #search-menu{
    display: flex;
    padding: 0 20%;
}
.search-box #search-menu label{
    flex: 1 30px;
    cursor: pointer;
    color: #fff;
    z-index: 1;
}
.search-box input{
    border-radius: 2em;
}
.search-box #search-menu-item{
    display: flex;
    flex-direction: row;
    padding-top: 1em;
}
.search-box #search-menu-item label{
    flex: 1 30px;
    cursor: pointer;
    z-index: 1;
}
.search-box #search-menu-item label span{
    color: #fff;
}

</style>