import websites from './websites.config.js'

let menus = [];

websites.forEach(website => {
  if(!menus.length){
    menus.push(
      {
        text: website.mainCategory,
        subMenus: [
          {
            text: website.subCategory,
          }
        ]
      }
    )
  }else{
    menus.forEach( (menu, index) => {
      if(menu.text === website.mainCategory && !menu.subMenus.some(item=>{ return item.text === website.subCategory})){
        menu.subMenus.push(
          {
            text: website.subCategory,
          }
        )
      }else if(menu.text != website.mainCategory && index+1 == menus.length){
        menus.push({
          text: website.mainCategory,
          subMenus:[
            {
              text: website.subCategory,
            }
          ]
        })
      }
    })
  }
})

menus = menus.map((x,i)=>{
    return {
        ...x,
        subMenus: (x.subMenus || []).map((y, j) =>{
            return {
                ...y,
                index: `${i}-${j}`,
            }
        }),
        index: `${i}` ,
    }

})

//去除重复的子菜单
let subMenus = [];
websites.forEach(website => {
  let subMenu = website.subCategory;
  if(!subMenus.includes(subMenu)){
    subMenus.push(subMenu);
  }
})

const menuIcons = [
  "el-icon-money",
  "el-icon-collection",
  "el-icon-knife-fork",
  "el-icon-truck",
  "el-icon-basketball",
]

export {menus, subMenus, menuIcons};