<template>
  <div class="show-websites">
    <div class="subMenu-card"
      v-for="subMenu in subMenus"
      :key="subMenu">
      <h1 :id="subMenu"><i class="el-icon-reading"></i>{{subMenu}}</h1>
      <el-row :gutter="10"
        >
        <el-col :xs="24" :sm="8" :md="6" :lg="4"
          v-for="website in subMenuWebsites(subMenu)"
          :key="website.text"
          >
          <div class="card-body">
            <el-card shadow="hover">
              <div class="title" :title="website.description">
                <a :href="website.url" target="_blank"><strong>{{website.text}}</strong></a>
                <p>{{website.description}}</p>
              </div>
             </el-card>
          </div>
        </el-col>
      </el-row>

    </div>
  </div>
</template>

<script>
import {subMenus} from '@/config/menusProcessing.js'
import websites from '@/config/websites.config.js'

export default{
  data(){
    return {
      subMenus,
      websites,
    }
  },
  computed: {
    subMenuWebsites(){
      return subMeue => {
          return this.websites.filter( item => {
            return item.subCategory == subMeue
          })
        }
    }
  }
}

</script>

