<template>
  <el-container style="min-height: 100%; height:100vh;">
    <el-aside style="width:auto;">
      <Menu :isMenuCollapse="isMenuCollapse"></Menu>
    </el-aside>
    <el-container>
      <el-header id="header">
        <el-button @click="isMenuCollapse=!isMenuCollapse"> <i :class="isMenuCollapse? 'el-icon-s-unfold':'el-icon-s-fold'" ></i> </el-button>
        <el-button @click="toggleTheme"><i :class="isDarkMode ? 'el-icon-moon-night' : 'el-icon-sunrise-1'"></i></el-button>
      </el-header>
      <el-main>
        <SearchBox></SearchBox>
        <Cards></Cards>
        <Backtop target='.el-main'></Backtop>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import {
    enable as enableDarkMode,
    disable as disableDarkMode,
} from 'darkreader';

enableDarkMode({
    brightness: 100,
    contrast: 90,
    sepia: 10,
});

// @ is an alias to /src
import Menu from '@/components/Menu.vue'
import Cards from '@/components/Cards.vue'
import SearchBox from '@/components/SearchBox.vue'
import Backtop from '@/components/Backtop.vue'

export default {
  name: 'Home',
  components: {
    Menu,
    Cards,
    SearchBox,
    Backtop,
  },
  data(){
    return {
      isMenuCollapse: false,
      isDarkMode: false,
    }
  },
  methods: {
    toggleTheme(){
      this.isDarkMode = !this.isDarkMode;
      if(this.isDarkMode){
        enableDarkMode({
          brightness: 100,
          contrast: 90,
          sepia: 10,
        });
      }else{
        disableDarkMode();
      }
    }
  }
}
</script>

<style scope>
.el-button{
  font-size: 24px;
  padding: 5px 8px;
}
.el-aside{
  height: 100vh;
}
#header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: right;
  background-color: #ededed;
  color: #333;
  max-height: 40px;
  padding: 0px;
}
.el-container .el-main{
  padding: 0px;
  height: 100vh;
}

</style>